@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.resume__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0.5rem 1rem;
    background-color: #fff;
    border-bottom: 1px solid #e6e6e6;
    height: 70px;
    width: 100%;
    box-shadow: 0px 4px 4px rgba(61, 60, 60, 0.25);
}

.resume__navbar-links{
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.resume__navbar-links_logo{
    margin: 1rem;
}
.resume__navbar-links_logo img{
    width: 100%;
    height: 60px;
    cursor: pointer;
}
.resume__navbar-links_container{
    display: flex;
    flex-direction: row;
}

.resume__navbar-links_container p,
 .resume__navbar-menu_container p{
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 1000;
    color: #10A6DE;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.resume__navbar-links_container p:hover,
 .resume__navbar-menu_container p:hover{
    color: #0c49ce;
}
.resume__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}
.resume__navbar-menu svg{
    cursor: pointer;
}
.resume__navbar-menu_container{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;

    text-align: end;
    background: white;
    padding: 1rem;
    position: absolute;
    top: 30px;
    right: 0;
    margin-top: 1rem;
    min-width: 200px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
    
}
.resume__navbar-menu_container p{
    margin: 1rem 0;
}
@media screen and (max-width: 1050px){
    .resume__navbar-links_container{
        display: none;
    }
    .resume__navbar-menu{
        display: flex;
    }
}
@media screen and (max-width: 700px){
    .resume__navbar{
        padding: 4rem;
    }
    
}
@media screen and (max-width: 550px){
    .resume__navbar{
        padding: 2rem;
    }
    .resume__navbar-menu_container{
        min-width: 150px;
        z-index: 1;
    }
}
.resume__header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.resume__header-contain{
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: 5rem;
}
.resume__header-contain h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;

}
.resume__header-contain p{
    font-family: var(--font-family);
    font-weight: normal;
    font-style: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
    justify-self: center;
    

}
.resume__header-container_button{
    display: flex;
    width: 25%;
    margin: 2rem 0 1rem;
    justify-content: center;
    align-items: center;
}
.resume__header-container_button button{
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;
    justify-self: center;

    background: #31a88a;
    color: #fff;
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: var(--font-family);
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
}
.resume__header-container_button button:hover{
    background: #2f9b8a;
}
.resume__header-image{ 
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
   

}
.resume__header-image img{
    width: 100%;
    height: 100%;
    
}
@media screen and (max-width: 1050px){
    .resume__header{
        flex-direction: column;
    }
    .resume__header-contain{
       margin: 0 0 3rem;
    }
    .resume__header-container_button{
        width: 100%;
        font-size: 18px;
        line-height: 24px;
    }
}
@media screen and (max-width: 650px){
    .resume__header h1{
        font-size: 48px;
        line-height: 60px;
    }
    .resume__header p{
        font-size: 16px;
        line-height: 24px;
    }
    .resume__header-container_button{
        width: 100%;
        font-size: 16px;
        line-height: 24px;
    }
}
@media screen and (max-width: 490px){
    .resume__header h1{
        font-size: 36px;
        line-height: 48px;
    }
    .resume__header p{
        font-size: 14px;
        line-height: 24px;
    }
    .resume__header-container_button{
        width: 100%;
        font-size: 12px;
        line-height: 16px;
    }
}

